    p.mb-5 El proceso de exportacin de bienes, se basa en las transacciones comerciales entre dos pases en las cuales existe la salida de mercancas del territorio aduanero nacional al exterior, con el fin de satisfacer las necesidades y requerimientos de clientes en el exterior. 

