    #t_1_1.titulo-segundo
      h2 1.1 Oferta exportable colombiana de bienes 

    p.mb-5 Colombia cuenta con una gran variedad de productos que, a lo largo de su trayectoria, ha comercializado con diferentes pases en el exterior. Desde 2002 Colombia ha exportado aproximadamente a 180 mercados, triplicando sus exportaciones en los ltimos 10 aos.  

    .row.mb-5.justify-content-around.align-items-center
      .col-lg-10
        .row
          .col-lg-6      
            figure.mb-5
              img(src='@/assets/tema1/img-4.svg')
          .col-lg-6.align-self-center
            p.mb-5 Las exportaciones tradicionales se refieren a los bienes que no sufren transformaciones durante su proceso. De acuerdo con la clasificacin del DANE (Departamento Administrativo Nacional de Estadsticas), los productos correspondientes a esta categora son: petrleo, carbn y ferronquel. Sin embargo, la mayora de los productos nacionales representan las exportaciones no tradicionales las cuales, en trminos generales, generan un amplio uso del manual de procedimientos y requieren innovacin y desarrollo para lograr un nivel competitivo de talla mundial. Colombia tiene una oferta exportable diversa que cobija productos post-industriales, manufacturas, prendas y servicios, con presencia en los cinco continentes (PROCOLOMBIA, 2021).

      p.mb-5 Por consiguiente, y con el fin de brindar herramientas a los actuales y futuros exportadores, Procolombia, entidad colombiana encargada de la promocin del turismo, inversin extranjera, exportaciones no minero energticas y la imagen del pas, desarroll un brochure en el cual se presenta al mundo la oferta exportable colombiana, la descripcin de las materias primas, caractersticas, portafolio de productos, pases con los cuales se han comercializado y estadsticas referentes.
      p.mb-5 Este brochure se compone de 5 captulos, cada captulo es una categora de los productos exportados. Por ejemplo: el captulo 1. Agroalimentos, lo componen todos los productos provenientes del agro exportables de Colombia (confitera, acucola, frutas procesadas, lcteos, snacks, panadera, galletera y molinera, cacao, cereales, aceite de palma, caf, bebidas sin alcohol, flores, frutas exticas, frutas tropicales, carne bovina, hierbas aromticas, frutas y hortalizas procesadas. (PROCOLOMBIA , 2021).

