    #t_1_3.titulo-segundo
      h2 1.3 Modalidades                
    p.mb-5 En el proceso de exportacin, se presentan las siguientes modalidades, las cuales se deben conocer y seleccionar cul de ellas aplicar en el proceso de exportacin segn los objetivos de la empresa (Ministerio de Hacienda y Crdito Pblico, 2019):      
    .row.mb-5.justify-content-around.align-items-center
      .col-lg-8
        .row.mb-5
          LineaTiempoD.color-acento-contenido
            p.text-small(numero="1" titulo="Exportacin definitiva") Es la modalidad de exportacin que regula la salida de mercancas nacionales o nacionalizadas, del territorio aduanero nacional para su uso o consumo definitivo en otro pas. 
            
            p.text-small(numero="2" titulo="Exportacin temporal para perfeccionamiento pasivo") Es la modalidad de exportacin que regula la salida temporal de mercancas nacionales o nacionalizadas, del territorio aduanero nacional, para ser sometidas a transformacin, elaboracin o reparacin en el exterior o en una zona franca, debiendo ser reimportadas dentro del plazo sealado en la declaracin de exportacin correspondiente, prorrogables por un ao ms. 
            
            p.text-small(numero="3" titulo="Exportacin temporal para reimportacin en el mismo estado") Es la modalidad de exportacin que regula la salida temporal de mercancas nacionales o nacionalizadas del territorio aduanero nacional, para atender una finalidad especfica en el exterior, en un plazo determinado, durante el cual debern ser reimportadas sin haber experimentado modificacin alguna, con excepcin del deterioro normal originado en el uso que de ellas se haga. 
            
            p.text-small(numero="4" titulo="Reexportacin") Es la modalidad de exportacin que regula la salida definitiva del territorio aduanero nacional, de mercancas que estuvieron sometidas a una modalidad de importacin temporal o a la modalidad de transformacin y/o ensamble.         

            p.text-small(numero="5" titulo="Reembarque") Es la modalidad de exportacin que regula la salida efectiva del Territorio Aduanero Nacional de mercancas procedentes del exterior, que se encuentran en lugar de arribo, en almacenamiento en un depsito habilitado, en un depsito franco o en centros de distribucin logstica internacional, que no han sido sometidas a una modalidad de importacin, ni han quedado en abandono.     

            p.text-small(numero="6" titulo="Exportacin por trfico postal y envos urgentes") Podrn ser objeto de exportacin, por esta modalidad, los envos de correspondencia, los envos que salen del territorio nacional por la red oficial de correos y los envos urgentes, siempre que su valor no exceda de cinco mil dlares de los Estados Unidos de Amrica (US$5.000) y requieran gil entrega a su destinatario.     
            
            p.text-small(numero="7" titulo="Exportacin de muestras sin valor comercial") La solicitud de autorizacin de embarque y la declaracin correspondiente, se tramitarn en la forma prevista en este Decreto para la exportacin definitiva, con embarque nico y datos definitivos.     
            
            p.text-small(numero="8" titulo="Exportaciones temporales realizadas por viajeros") Sern objeto de esta modalidad de exportacin las mercancas nacionales o nacionalizadas que lleven consigo los viajeros que salgan del pas y que deseen reimportarlas a su regreso en el mismo estado, sin pago de tributos. No estarn comprendidos en esta modalidad y no sern objeto de declaracin, los efectos personales que lleven consigo los viajeros que salgan del territorio aduanero nacional.

            p.text-small(numero="9" titulo="Exportacin de menajes y, Programas Especiales de Exportacin") Sern objeto de esta modalidad de exportacin los menajes de los residentes en el pas, que salen del territorio aduanero nacional para fijar su residencia en el exterior. 
