        p.text-bold Primer paso
        p El primer paso para realizar la exportacin de bienes consiste en el registro antes Cmara y Comercio y la DIAN para la obtencin del RUT y NIT como exportador o productor colombiano. Es importante resaltar que el RUT es el Registro nico Tributario, que constituye el mecanismo nico para identificar, ubicar y clasificar las personas y entidades que tengan la calidad de contribuyentes declarantes del impuesto sobre la renta y no contribuyentes declarantes de ingresos y patrimonio; los responsables del Rgimen Comn y los pertenecientes al rgimen simplificado; los agentes retenedores; los importadores, exportadores y dems usuarios aduaneros, y los dems sujetos de obligaciones administradas por la Direccin de Impuestos y Aduanas Nacionales, respecto de los cuales esta requiera su inscripcin (DIAN, 2021).  

    p.mb-5 Por otra parte, el NIT, El Nmero de Identificacin Tributaria (NIT), es asignado por la Direccin de Impuestos y Aduanas Nacionales (DIAN) y permite la individualizacin inequvoca de los inscritos, para todos los efectos, en materia tributaria, aduanera y de control cambiario y, en especial, para el cumplimiento de las obligaciones de la misma naturaleza (DIAN, 2021).

    .row.mb-5.justify-content-around.fnd-1

      .col-lg-8.align-self-center.px-2.tt1
        p.text-bold Segundo paso
        p En segundo lugar, el exportador debe realizar la identificacin de la subpartida arancelaria del producto a exportar. Este proceso, lo puede realizar por medio del arancel de aduanas, servicio en lnea de la DIAN en Colombia y le permitir identificar los requerimientos que tienen los productos para ingresar a otros pases. 

      .col-lg-4      
        figure
          img.img-380(src='@/assets/tema1/img-10.svg')

    .row.mb-5.justify-content-around.fnd-1
      .col-lg-4      
        figure
          img.img-380(src='@/assets/tema1/img-11.svg')
      .col-lg-8.align-self-center.px-2.tt1
        p.text-bold Tercer paso
        p Seguido a esto, el exportador identifica y evala los mercados potenciales para la exportacin. Este aspecto es muy importante, porque permite identificar los gustos y preferencias de los clientes al igual que las condiciones de acceso a los pases, condiciones relacionadas con el producto, acuerdos existentes y condiciones de rutas, modos y medios de transporte. o.

    .row.mb-5.justify-content-around.fnd-1

      .col-lg-8.align-self-center.px-2.tt1
        p.text-bold Cuarto paso
        p Posterior a los tres pasos mencionados anteriormente, es momento de enviar la cotizacin al comprador en el extranjero, en la cual se contemplan las caractersticas del producto, cantidades, precios y condiciones de entrega para la aceptacin por parte del mismo y el establecimiento del medio de pago, dentro de los cuales estn: pago anticipado, pago directo, cobranzas documentarias y la carta de crdito.

      .col-lg-4      
        figure
          img.img-380(src='@/assets/tema1/img-12.svg')

    p.mb-5 Tambin, se remite la factura comercial, se contrata el transporte y seguro segn lo pactado, se realizan los trmites ante la Ventanilla nica de Comercio Exterior (VUCE), herramienta de facilitacin del comercio en Colombia, en la cual los exportadores e importadores realizan el trmite electrnico de autorizaciones, certificaciones, permisos o vistos buenos, en las diferentes entidades competentes encargadas de los trmites de exportacin e importacin. Estos ltimos, siendo uno de los trmites ms importantes del proceso de exportacin de ciertos productos, a los que se les exige que el exportador se encuentre inscrito en la entidad encargada de su control y/o del otorgamiento del visto bueno para ser exportado, teniendo en cuenta las normas vigentes para cada producto. Tambin, se solicita y presenta la autorizacin de embarque, autorizacin que otorga la DIAN para que la mercanca pueda salir del territorio aduanero nacional. 


