    p.mb-5 Actualmente, gracias a los procesos de globalizacin, se han desarrollado tratados y acuerdos comerciales entre pases que permiten la comercializacin de bienes y servicios sin mayores obstculos y restricciones. Algunos de estos requieren que los productos a exportar cumplan con los requerimientos y caractersticas deseables en el pas de origen, al igual que los productos cuenten con el certificado de ser producidos en uno de los pases participantes de la negociacin, dicho documento se denomina certificado de origen. 
