            p Todas las mercancas exportadas desde Colombia estn sometidas o sujetas a la inspeccin de las autoridades, que determinar el mtodo a utilizar de los disponibles: inspecciones fsicas, con biosensores (como el uso de perros entrenados) o mediante escneres. 
        
      
